}

/*清除浮动代码 '父元素'*/
.clearfloat:after {
  display: block;
  clear: both;
  content: " ";
  visibility: hidden;
  height: 0;
}
.clearfloat {
  zoom: 1;
}

/*去除ios默认样式*/
input button {
  /*IOS*/
  -webkit-appearance: none;
  background: none;
  border: none;
  outline: none;
}

/*public 省略号*/
.publicEllipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*居中*/
.centered {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  margin: auto;
}

/*省略号 2行  */
.apostrophe {
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
}
